* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header__background__img {
  background-image: url("../hero.7b866a53.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 56vw;
  position: relative;
  overflow: hidden;
}

.header__background__img:before {
  content: "";
  z-index: 1;
  background: #0006;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.header__background__img .header__background__img__txt {
  z-index: 2;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header__background__img .header__background__img__txt span {
  color: #15eb20;
}

.header__background__img .header__background__img__txt h1 {
  color: #f9f9f9;
  margin: 0;
  font-size: 3rem;
  line-height: 1.2;
}

.section__naoFiqueFora__background {
  background-image: url("../NãoFiqueDeFora.d9601b0c.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 56vw;
  position: relative;
}

.section__naoFiqueFora__background__txt {
  text-align: center;
  color: #f9f9f9;
  z-index: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.section__naoFiqueFora__background__txt h1 {
  margin: 0;
  font-size: 50px;
}

.section__naoFiqueFora__background__button {
  text-align: center;
  z-index: 2;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.section__naoFiqueFora__background__button a {
  color: #fff;
  background-color: #007bff;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.section__naoFiqueFora__background__button a:hover {
  background-color: #0056b3;
}

.section__naoFiqueFora__background:before {
  content: "";
  z-index: 1;
  background: #0006;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media (width <= 640px) {
  body {
    background-color: #f2ece4;
  }

  .header__background__img {
    background-image: none;
  }

  .header__background__img .header__background__img__txt span {
    color: #594a3c;
  }

  .header__background__img .header__background__img__txt h1 {
    color: #594a3c;
    font-size: 25px;
  }

  .header__background__img:before {
    display: none;
  }

  .section__naoFiqueFora__background {
    background-image: none;
  }

  .section__naoFiqueFora__background__txt h1, .section__naoFiqueFora__background:before {
    display: none;
  }

  .section__naoFiqueFora__background__button {
    text-align: center;
    position: absolute;
    bottom: 50%;
    left: 50%;
  }

  .section__naoFiqueFora__background__button a {
    background-color: #594a3c;
    padding: 10px;
    font-size: 10px;
  }

  .section__naoFiqueFora__background__button a:hover {
    color: #594a3c;
    background-color: #d9c2a7;
  }
}
